<template>
    <div style="" class="m2item">

        <span v-html="replaced"></span>
        <ol type="a">
            <li v-for="(op, index) in options">
                <!--<button @click="click(index)">{{op}}</button>-->
                <label>
                    <input :name="nameRadio" type="radio"/>
                    <span @click="click(index)">{{ concatenate(op)}}</span>
                </label>
            </li>
        </ol>
    </div>
</template>
<script>
    /* eslint-disable */
    import Vue from 'vue'
    import "materialize-css/dist/css/materialize.css"
    import "materialize-css/dist/js/materialize.min"

    export default {
        name: 'M2Item',
        components: {},
        props: {
            options: Array,
            sentence: String,
            nameRadio: String
        },
        data: function () {
            return {
                toReplace: '________',
                newString: '',
                selectedIndex: -1
            }
        },
        computed: {
            replaced(){
                console.log("replaced");
                this.newString = this.sentence;
                if (this.selectedIndex === -1) {
                    this.options[0].forEach(op => this.newString = this.newString.replace("###", this.toReplace));
                } else {
                    this.options[this.selectedIndex].forEach(op => this.newString = this.newString.replace("###", this.getSpan(op)));
                }
                return this.newString;
            }
        },
        methods: {
            click(index){
                this.selectedIndex = index;
            },
            getSpan(str){
                return '<span class="bold">' + str + '</span>'
            },
            concatenate(stringArray){
                let separator = '';
                let concatenated = '';
                stringArray.forEach(str => {
                    concatenated += separator + str;
                    separator = " - ";
                });
                return concatenated;
            }
        },
        mounted: function () {
            console.log("mounted")
        }
    }
</script>

