<script>
    /* eslint-disable */
    import Vue from 'vue'
    import InlineSelect from './InlineSelect.vue';
    //se divide el string en arrays ej: "Me llamo ###" -->  ["Me llamo","###"]

    export default {
        name: 'M1Item',
        components: {
            InlineSelect
        },
        render: function (createElement) {
            let newArray = this.sentence.split(/(###)/g);
            let indexOptions = 0;
            newArray = newArray.map(str => {
                console.log(str);
                if (str === '###') {
                    let tagNameComponent = 'inline-select';
                    let propsToSend = {
                        options: this.options[indexOptions],
                        groupOptions: indexOptions
                    };
                    indexOptions++;
                    str = createElement(tagNameComponent, {props: propsToSend});
                    return str;
                } else {
                    return str;
                }
            });
            return createElement('div', newArray)
        },
        props: {
            sentence: String,
            options: Array
        },
        data: () => {
            return {}
        },
    }
</script>

