<template>
    <label style="" class="inlineSelect">
        <!--{{groupOptions}}-->
        <span v-for="(option,index) in options">
            <span
                class="spanOption"
                v-bind:class="{  selectedClass : (selectedIndex === index) }"
                @click="click(index)">{{option}}</span>
                {{ index + 1 < options.length ? ' / ' : '' }}
        </span>

        <!--<pre>-->
            <!--{{options}}-->
        <!--</pre>-->
    </label>
</template>
<script>
    /* eslint-disable */
    import Vue from 'vue'
    import "materialize-css/dist/css/materialize.css"
    import "materialize-css/dist/js/materialize.min"

    export default {
        name: 'M1Item',
        components: {},
        props: {
            options: Array,
            groupOptions: Number
        },
        data: function () {
            return {
                selectedIndex: -1,
            }
        },
        computed: {
            replaced(){
            }
        },
        methods: {
            click(indexSelected){
                this.selectedIndex = indexSelected;
//                console.log("inlineSelect groupO,idxSelected :  " + this.groupOptions + "," + indexSelected);
                this.$emit('changedIndexSelected', this.groupOptions, indexSelected)
            }
        },
        mounted: function () {
//            console.log("inlineSelect")
//            console.log(this.options)
//            console.log(this.groupOptions)
        }
    }
</script>

